@import "../inc/config";

main.screen_chat {
  position: relative;
  flex-direction: column;

  .chat-form {

    position: fixed;
    bottom: 50px;
    padding:10px 5px;
    left: 0;
    right: 0;
    display: flex;
    min-height: 50px;
    width: 100%;
    background: $color0;
    z-index: 99;
    form {
      padding: 0 10px;
      width: 100%;
      display: flex;
    }
    input {
        min-height: 50px;
    }
  }

  .chat-window {
    display: flex;
    flex-direction: column;
    flex: 1;
    margin-bottom: 100px;
    .chat-buble {
      padding: 10px;
      display: flex;
      background: $color1;
      margin:5px 10px;
      border-radius: 10px;
      font:400 13px/14px $font;
      user-select: all;
      .info {
        display: flex;
        width: 32px;
        .avatar {
          background: red;
          size:32px;
          border-radius: 50%;
        }
      }
      .chat-body {
        padding-left: 20px;
        flex:1;
        display: flex;
        flex-direction: column;
        .chat-top {
          display: flex;
          .user {
            letter-spacing: .5px;
            font:600 13px/12px $font;
            margin-right: 5px;
            color:$color2;
          }
          .time {
            font:400 13px/12px $font;
                      text-decoration: underline;
          }
        }
        .chat-bottom {

          font:400 12px/14px $font;
            letter-spacing: .5px;
          margin-top: 8px;
        }
      }
      &.he {
        background: lighten($color1, 6);
      }

    }
  }
}
